@use '../abstracts' as a;

.contacts {
    padding-top: 100px;

    &__container {
        display: flex;
        justify-content: space-between;
        margin-top: 30px;

        &__links {
            &--cv {
                padding: 30px;
                font-weight: a.$medium;
                background-color: a.$primary;
                width: 100%;
                display: flex;
                justify-content: space-between;
                align-items: center;

                a {
                    display: block;
                    padding: 10px 20px;
                    background-color: a.$black;
                    color: #fff;
                }
            }
        }

        form {
            width: 300px;
            display: flex;
            flex-direction: column;

            input, textarea {
                background-color: a.$grey-light;
                padding: 10px 20px;
                color: a.$black;
            }

            textarea {
                min-height: 150px;
            }

            >* {
                border: none;
                font-family: a.$poppins;
                margin-bottom: 10px;
                font-size: a.$paragraph;
            }

            button {
                width: fit-content;
                background-color: a.$primary;
                padding: 10px 25px;
            }
        }
    }
}